import Fade from "react-reveal/Fade";
import Bounce from "react-reveal/Bounce";
import Jello from "react-reveal/Jello";
import RubberBand from "react-reveal/RubberBand";
import Slide from "react-reveal/Slide";

import Tilt from "react-parallax-tilt";

const Intro = () => {
  const textRef = useRef();
  useEffect(() => {
    init(textRef.current, {
      showCursor: true,
      strings: [
        "Full Stack Engineer",
        "Open Source Enthusiast",
        "Freelance Developer",
      ],
      backDelay: 1200,
    });
  }, []);

  return (
    <Zoom duration={1000}>
      <IntroSection>
        <ImgWrapper>
          <div>
            <Zoom delay={1700}>
              <img src="/assets/me.png" alt="Profile" />
            </Zoom>
          </div>
        </ImgWrapper>

        <IntroText>
          <Text>
            <Zoom delay={1600} cascade>
              Hi there, I'm
            </Zoom>
          </Text>
          <GradientText>
            <Tilt trackOnWindow={true} tiltMaxAngleX={3} tiltMaxAngleY={3}>
              <Bounce up delay={2500} duration={1000}>
                <RubberBand delay={3100} duration={1500} cascade>
                  Sahaj Dave
                </RubberBand>
              </Bounce>
            </Tilt>
          </GradientText>

          <Fade right delay={2500}>
            <AnimatedText>
              <span ref={textRef}></span>
            </AnimatedText>
          </Fade>
        </IntroText>
        <DownArrowWrapper href="#education">
          <Bounce down delay={2800}>
            <DownArrowImage src="assets/down.svg" alt="down arrow" />
          </Bounce>
        </DownArrowWrapper>
      </IntroSection>
    </Zoom>
  );
};

export default Intro;

