import { ThemeProvider } from "styled-components";
import Intro from "./components/Intro";
import Education from "./components/Education";
import { darkTheme, orangeTheme } from "./themes";
import WebFont from "webfontloader";
import { useEffect, useState } from "react";
import Contact from "./components/Contact";
import PropagateLoader from "react-spinners/PropagateLoader";
import Header from "./components/Header";
import { data } from "./data.js";
import Project from "./components/Project";

function App() {
  const [loader, setLoader] = useState(false);
  const [theme, setTheme] = useState(true);

  useEffect(() => {
    setLoader(true);
    WebFont.load({
      google: {
        families: ["Roboto", "Poppins", "La Belle Aurore"],
      },
      active: function () {
        setLoader(false);
        console.log("SET LOADER FALSE");
      },
    });
  }, []);

  return (
    <ThemeProvider theme={theme ? darkTheme : orangeTheme}>
      {loader ? (
        <div className="react-spinner">
          <PropagateLoader loading={loader} size={15} />
        </div>
      ) : (
        <div className="App">
          <Header
            toggleTheme={() => {
              setTheme(!theme);
            }}
          />
          <Intro />
          <Education />
          <Contact />

          {/* {data.map((project) => {
            return (
              <Project
                key={project.id}
                title={project.title}
                images={project.img}
                description={project.description}
                giturl={project.githubURL}
              />
            );
          })} */}
        </div>
      )}
    </ThemeProvider>
  );
}

export default App;
