import styled from "styled-components";
import Section from "./layout/Section";
import Container from "./layout/Container";
import Title from "./layout/Title";
import Cursive from "./layout/Cursive";
import React from "react";
import Bounce from "react-reveal/Bounce";
import Fade from "react-reveal/Fade";
import Zoom from "react-reveal/Zoom";
import withReveal from "react-reveal/withReveal";

const Contact = () => {
  const handleFormSubmit = (e) => {
    e.preventDefault();
    let emailbody = document.getElementById("emailBody").value;
    let emailSubject = document.getElementById("emailSubject").value;

    if (emailSubject === "" || emailbody === "") {
      return;
    }
    document.location.href =
      "mailto:sahajdavework@pm.me?subject=" +
      encodeURIComponent(emailSubject) +
      "&body=" +
      encodeURIComponent(emailbody);
  };

  return (
    <ContactSection>
      <TitleContainer>
        <ContactTitle>
          <Bounce delay={600} cascade>
            Contact
          </Bounce>
        </ContactTitle>
      </TitleContainer>
      <ContactBody>
        <MapContainer>
          <Zoom delay={600} bottom>
            <iframe
              title="Map Location"
              frameborder="0"
              scrolling="no"
              src="https://www.openstreetmap.org/export/embed.html?bbox=70.79589843750001%2C16.641455036937753%2C76.92626953125001%2C20.354927584117682&amp;layer=mapnik&amp;marker=18.508261205882686%2C73.861083984375"
            ></iframe>
          </Zoom>
        </MapContainer>
        <FormContainer>
          <div>
            <Fade up delay={600}>
              <Cursive>&lt;p&gt;</Cursive>
              <p>
                I'm interested in freelance opportunities - especially ambitious
                or large projects. However, if you have other request or
                question, don't hesitate to use the form
              </p>
              <Cursive>&lt;/p&gt;</Cursive>
            </Fade>
          </div>
          <form onSubmit={handleFormSubmit} action="">
            <Fade up delay={1000}>
              <Cursive>&lt;form&gt;</Cursive>
              <input id="emailSubject" type="text" placeholder="Subject" />
              <textarea
                id="emailBody"
                placeholder="Message"
                cols="30"
                rows="10"
              ></textarea>

              <ButtonContainer>
                <Button type="submit">Contact Me</Button>
              </ButtonContainer>

              <Cursive>&lt;/form&gt;</Cursive>
            </Fade>
          </form>
        </FormContainer>
      </ContactBody>
    </ContactSection>
  );
};

export default Contact;

const ContactSection = styled(Section)`
  flex-direction: column;
  align-items: center;
  justify-content: enter;
`;

const TitleContainer = styled(Container)`
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;
const ContactTitle = withReveal(styled(Title)``, <Fade left />);

const ContactBody = styled(Container)`
  @media screen and (min-width: 768px) {
    flex-direction: row;

    align-items: stretch;
    justify-content: flex-start;
  }

  height: 100%;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
`;

